@import url(https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  margin: 0%;
  padding: 0%;
  font-family: "Poppins", sans-serif;
  background-color: black;
}

header {
  display: flex;
}

.video {
  position: absolute;
  z-index: -1;
  width: 100%;
}

@media (max-width: 960px) {
  .video {
    display: none;
  }
}

.MuiSvgIcon-root {
  display: none;
  color: white;
}

@media (max-width: 960px) {
  .kadajname {
    display: none;
  }

  .MuiSvgIcon-root {
    display: block;
    margin-right: 1em;
    color: rgb(255, 255, 255);
    width: 1.5em;
    height: 1.5em;
  }
  .MuiMenu-paper {
    background-color: rgb(16, 16, 15);
    box-shadow: 3px 3px 5px 6px rgb(0, 0, 0);
    border-radius: 0.5em;
  }

  .MuiMenuItem-root {
    font-weight: 600;
    color: white;
  }

  .MuiMenu-list {
    display: block;
  }

  .MuiIconButton-root {
    padding: 6px;
  }
}

.twitch {
  font-size: 40px;
  margin-right: 0.5em;
  display: flex;
}

.menuright {
  display: none;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
  }
}

@-webkit-keyframes fadeInBot {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
}

@keyframes fadeInBot {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
}

.videoguldan {
  display: none;
}

@media (max-width: 640px) {
  .container {
    /* background-image: url(../Imagenes/guldan3.jpg); */
    background-size: 30em;
    background-position: 3em 3em 3em;
    background-repeat: no-repeat;
    display: flex;
    height: 60vh;
    justify-content: flex-start;
    flex-direction: column;
  }

  .videoguldan {
    display: block;
    position: absolute;
    z-index: -1;
    width: 100%;
    -webkit-filter: brightness(60%);
            filter: brightness(60%);
  }

  .menuright {
    display: flex;
    margin-top: 10px;
    flex-direction: row-reverse;
  }

  .containerNest {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    margin-left: 40px;
    justify-content: center;
  }

  .firstsection {
    display: flex;
    flex-direction: column;
    -webkit-animation: fadeIn ease 2s;
            animation: fadeIn ease 2s;
  }

  @-webkit-keyframes fadeIn {
    0% {
      opacity: 0;
      -webkit-transform: translateX(-20px);
    }
    100% {
      opacity: 1;
      -webkit-transform: translateX(0);
    }
  }

  @keyframes fadeIn {
    0% {
      opacity: 0;
      -webkit-transform: translateX(-20px);
    }
    100% {
      opacity: 1;
      -webkit-transform: translateX(0);
    }
  }

  .firstsection h1 {
    font-size: 3em;
    margin-bottom: 20px;
  }

  .firstsection h1 span {
    color: rgb(122, 240, 12);
  }

  .firstsection h2 {
    color: white;

    font-size: 2em;
  }

  .secondsection p {
    font-size: 1em;
  }

  .secondsection p span {
    color: rgb(122, 240, 12);
  }
}

